import { Component, OnInit } from '@angular/core';
import { EditorConfig } from '../editor/module/editor-config';
import { ElNotificationService  } from 'element-angular';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {
  config = new EditorConfig();
  markdown: string;
  title: string;
  totgle: boolean;
  tags: any; // 博客标签
  article: any; // 博客类型
  articlesorts: any; // 博客分类
  tagItem: any = [
    {
      value: 'Angular5',
      label: 'Angular5'
    },
    {
      value: 'Vue2.0',
      label: 'Vue2.0'
    },
    {
      value: 'Angular5 路由',
      label: 'Angular5 路由'
    },
    {
      value: 'HTML5',
      label: 'HTML5'
    },
    {
      value: 'CSS3',
      label: 'CSS3'
    },
    {
      value: 'TypeScript',
      label: 'TypeScript'
    },
    {
      value: '其他',
      label: '其他'
    }
  ];
  article_type: any = [
    {
      value: '原创',
      label: '原创'
    },
    {
      value: '转载',
      label: '转载'
    },
    {
      value: '翻译',
      label: '翻译'
    },
  ];
  article_sort: any = [
    {
      value: '前端',
      label: '前端'
    },
    {
      value: '后端',
      label: '后端'
    },
    {
      value: '手记',
      label: '手记'
    },
    {
      value: '其他',
      label: '其他'
    },
  ];
  constructor(
    private notify: ElNotificationService
  ) {
    this.totgle = false;
  }

  ngOnInit() {
  }

  // 同步属性内容
  syncModel(str): void {
    this.markdown = str;
  }
  // 发布博客
  publish(type) {
    if (this.title === undefined || this.title === '') {
      this.notify[type]('标题不能为空！');
    }
    if (!(this.title === '' || this.title === undefined) && (this.markdown === undefined || this.markdown === '')) {
      this.notify[type]('内容不能为空！');
    }
    if ( !(this.title === '' || this.title === undefined) && !(this.markdown === undefined || this.markdown === '')) {
    }
  }
  handle(e) {
    console.log(e);
  }
  handlearticle(e) {
    console.log(e);
  }
  sorts(e) {
    console.log(e);
  }
}



