export class EditorConfig {
  public width = '100%';
  public height = '560';
  public path = 'assets/markdown/lib/';
  public codeFold: true;
  public searchReplace = true;
  public toolbar = true;
  public emoji = true;
  public taskList = false;
  public tex = true; // 开启科学公式TeX语言支持，默认关闭
  public readOnly = false;
  public tocm = true;
  public watch = true;
  public previewCodeHighlight = true; // 开启预览 HTML 的代码块高亮，默认开启
  public saveHTMLToTextarea = true;
  public markdown = '';
  public flowChart = true;
  public syncScrolling = true;
  public sequenceDiagram = true;
  public imageUpload = true;
  public imageFormats = ['jpg', 'jpeg', 'gif', 'png', 'bmp', 'webp'];
  public imageUploadURL = '';
  public placeholder = '记录下点什么吧！';
  public theme = 'default';
  toolbarIcons() {
    return ['undo', '|', 'bold', 'italic', '|', 'h1', 'h2', 'h3', 'h4', 'h5', 'h6',  '|', 'code', 'preformatted-text', 'code-block', '|',  'list-ol', 'list-ul', 'hr', '|', 'link', 'image', 'emoji', '||', 'preview', 'clear', 'search'];
  }  // 自定义工具栏
  constructor() {
  }
}



